import React, { Component } from 'react';
import {BrowserRouter, HashRouter, Route, Switch} from "react-router-dom";

import "assets/css/nucleo-icons.css";
import "assets/scss/blk-design-system-react.scss?v=1.2.0";
import "assets/demo/demo.css";

import Index from "views/Index.js";
import MainView from "views/MainView.js";
import WhatIsView from "views/WhatIsView.js";
import LandingPage from "views/examples/LandingPage.js";
import RegisterPage from "views/examples/RegisterPage.js";
import ProfilePage from "views/examples/ProfilePage.js";
import PreviousEffortsView from "./views/PreviousEffortsView.js";
import ConcernedView from "./views/ConcernedView";
import FutureView from "./views/FutureView";
import ScrollToTop from "./components/PageHeader/ScrollToTop";


class App extends Component {


    render() {
        return (
            <HashRouter basename={"/"}>
                {/*<ScrollToTop/>*/}
                <Switch>
                    <Route path="/components" render={(props) => <Index {...props} />} />
                    <Route
                        path="/landing-page"
                        render={(props) => <LandingPage {...props} />}
                    />
                    <Route
                        path="/register-page"
                        render={(props) => <RegisterPage {...props} />}
                    />
                    <Route
                        path="/profile-page"
                        render={(props) => <ProfilePage {...props} />}
                    />
                    <Route
                        path="/what-is"
                        component={WhatIsView}
                    />
                    <Route
                        path="/previous-efforts"
                        render={(props) => <PreviousEffortsView {...props} />}
                    />
                    <Route
                        path="/concerned"
                        render={(props) => <ConcernedView {...props} />}
                    />
                    <Route
                        path="/future"
                        render={(props) => <FutureView {...props} />}
                    />
                    <Route
                        exact path="/"
                        render={(props) => <MainView {...props} />}
                    />

                    {/*<Redirect from="/" to="/components" />*/}
                </Switch>
            </HashRouter>

        );
    }
}
export default App;
